@font-face {
    font-family: "Matter-LightItalic";
    src: url("../../tipografias/Matter-LightItalic.otf");
}
@font-face {
    font-family: "Matter-Regular";
    src: url("../../tipografias/Matter-Regular.otf");
}
@font-face {
    font-family: "Matter-Medium";
    src: url("../../tipografias/Matter-Medium.otf");
}
@font-face {
    font-family: "MediumItalic";
    src: url("../../tipografias/Matter-MediumItalic.otf");
}

.carritoCierre{
    margin-top: 170px;
}

.borrarStk{
    width: 100%;
    height: 100%;

    background-color: transparent;
    border: transparent;

    font-family: "Matter-LightItalic";
    font-size: 1.1rem;

    transition: transform 1s;
}
.borrarStk:hover{
    color: red;
    transform: scale(1.5, 1.5);
}

.itemNombre{
    font-family: "Matter-Medium";
    font-size: 1rem;
}

.itemPrecio{
    font-family: "Matter-Regular";
}

.itemCantidad{
    font-family: "Matter-LightItalic";
}

.totalPrecio{
    font-family: "Matter-Medium";
    font-size: 1.2rem;
}

.vaciarCarrito{
    width: 50px;
    height: 50px;

    background-color: transparent;
    border: 2px solid black;
    border-radius: 50%;

    color: black;
    font-family: "MediumItalic";
    font-size: 1.5rem;
}
.vaciarCarrito:hover{
    background-color: black;
    color: white;
}

@media (min-width: 576px){

    .carritoCierre{
        margin-top: 210px;
    }
}

@media (min-width: 992px){

    .carritoCierre{
        margin-top: 250px;
    }
}

@media (min-width: 1400px){

    .carritoCierre{
        margin-top: 200px;
    }
}